.mood-chat {
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  height: 400px;
  margin: 1rem 0;
  overflow: hidden;
  border: 1px solid rgba(234, 234, 255, 0.5);
  animation: fadeIn 0.5s ease-in-out;
  transition: all 0.3s ease;
  max-width: 650px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.mood-chat:hover {
  box-shadow: 0 10px 35px rgba(0, 0, 0, 0.15);
  transform: translateY(-3px);
}

.chat-header {
  padding: 1rem;
  background: linear-gradient(135deg, #6e8efb 0%, #a777e3 100%);
  color: white;
  text-align: center;
}

.chat-title {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chat-icon {
  margin-right: 0.5rem;
  font-size: 1.4rem;
}

.chat-subtitle {
  font-size: 0.85rem;
  opacity: 0.9;
}

.chat-messages {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  background-color: #f9f9ff;
  scroll-behavior: smooth;
}

/* Enhance scrollbar appearance */
.chat-messages::-webkit-scrollbar {
  width: 8px;
}

.chat-messages::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.chat-messages::-webkit-scrollbar-thumb {
  background: rgba(138, 110, 251, 0.5);
  border-radius: 10px;
}

.chat-messages::-webkit-scrollbar-thumb:hover {
  background: rgba(138, 110, 251, 0.7);
}

.message {
  max-width: 80%;
  padding: 0.75rem 1rem;
  border-radius: 18px;
  position: relative;
  animation: messageAppear 0.3s forwards;
}

.bot-message {
  align-self: flex-start;
  background-color: #f0f0f0;
  border-bottom-left-radius: 4px;
}

.user-message {
  align-self: flex-end;
  background: linear-gradient(135deg, #6e8efb 0%, #a777e3 100%);
  color: white;
  border-bottom-right-radius: 4px;
}

.message-text {
  font-size: 0.95rem;
  line-height: 1.4;
}

.message-time {
  font-size: 0.7rem;
  opacity: 0.7;
  text-align: right;
  margin-top: 0.25rem;
}

.typing-indicator {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  padding: 0.5rem 0;
}

.typing-indicator span {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: #a0a0a0;
  display: inline-block;
  animation: typing 1s infinite ease-in-out;
}

.typing-indicator span:nth-child(1) {
  animation-delay: 0s;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%,
  100% {
    transform: translateY(0);
    opacity: 0.5;
  }
  50% {
    transform: translateY(-5px);
    opacity: 1;
  }
}

@keyframes messageAppear {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.chat-input-form {
  display: flex;
  padding: 1rem;
  background-color: #f9f9f9;
  border-top: 1px solid #ececec;
}

.chat-input-form input {
  flex: 1;
  padding: 0.75rem 1rem;
  border: 1px solid #dcdcdc;
  border-radius: 24px;
  font-size: 0.95rem;
  transition: border-color 0.2s ease;
}

.chat-input-form input:focus {
  border-color: #a777e3;
  outline: none;
  box-shadow: 0 0 0 2px rgba(167, 119, 227, 0.2);
}

.chat-input-form button {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background: linear-gradient(135deg, #6e8efb 0%, #a777e3 100%);
  color: white;
  margin-left: 0.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.chat-input-form button:disabled {
  background: #e0e0e0;
  cursor: not-allowed;
  opacity: 0.7;
}

.chat-input-form button svg {
  width: 20px;
  height: 20px;
}

.chat-input-form button:hover:not(:disabled) {
  transform: scale(1.05);
  box-shadow: 0 2px 8px rgba(167, 119, 227, 0.4);
}

/* Enhanced send button styles */
.send-button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 70px;
  padding: 0.75rem;
  transition: all 0.2s ease;
  background: linear-gradient(135deg, #6e8efb 0%, #a777e3 100%);
  border: none;
  border-radius: 30px;
  color: white;
  cursor: pointer;
}

.send-button:disabled {
  background: #e0e0e0;
  cursor: not-allowed;
  opacity: 0.7;
}

.send-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.3rem;
}

.send-text {
  font-size: 0.85rem;
  font-weight: 500;
}

.send-button:active:not(:disabled) {
  transform: scale(0.95);
}

/* Improved animation for send button */
@keyframes sendPulse {
  0% {
    box-shadow: 0 0 0 0 rgba(138, 110, 251, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(138, 110, 251, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(138, 110, 251, 0);
  }
}

.send-button:not(:disabled) {
  animation: sendPulse 2s infinite;
}

/* Make the input more responsive to typing state */
.chat-input-form input:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
  color: #888;
}

/* Add a subtle effect to show when bot is responding */
.chat-input-form input[disabled]::placeholder {
  color: #a777e3;
  font-style: italic;
  animation: pulsePlaceholder 1.5s infinite alternate;
}

@keyframes pulsePlaceholder {
  from { opacity: 0.7; }
  to { opacity: 1; }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Enhanced message styling */
.bot-message {
  position: relative;
}

.bot-message::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: -8px;
  width: 16px;
  height: 16px;
  background-color: #f0f0f0;
  transform: rotate(45deg);
  border-bottom-left-radius: 4px;
}

.user-message::before {
  content: '';
  position: absolute;
  bottom: 0;
  right: -8px;
  width: 16px;
  height: 16px;
  background: linear-gradient(135deg, #6e8efb 0%, #a777e3 100%);
  transform: rotate(45deg);
  border-bottom-right-radius: 4px;
}
